Rex(function ({ atom, radio }) {

  @atom.component('lazy-load', 'img')
  class Item {

    @atom.created
    build() {
      this.lazySrc = this.getAttribute('src');
      this.setAttribute('src', '');
    }

    @atom.attached
    load() {
      setTimeout(() => this.setAttribute('src', this.lazySrc), 10000)
    }
    
  }

});